import React from "react";
import homeData from "./HomeData";
import Bio from "./Bio/Bio";
import { motion } from "framer-motion";
import { pageAnimation, pageTransition } from "../../PageTransition/index";

import "./Home.scss";

const Home = (props) => {
  return (
    <motion.div
      className="home"
      initial="out"
      animate="in"
      exit="out"
      variants={pageAnimation}
      transition={pageTransition}
    >
      {homeData.map((item, index) => (
        <Bio
          profile={item.profileImg}
          resume={item.resumeImg}
          heading={item.heading}
          headingSpan={item.headingSpan}
          paragraphOne={item.paragraphOne}
          paragraphTwo={item.paragraphTwo}
          hireBtn={item.hireBtn}
          resumeBtn={item.resumeBtn}
          key={index}
        />
      ))}
    </motion.div>
  );
};

export default Home;
