import React, { useEffect } from "react";
import { projectsData } from "./ProjectsData";
import Project from "./Project/Project";
import { motion } from "framer-motion";
import { pageAnimation, pageTransition } from "../../PageTransition/index";

import mixitup from "mixitup";

import "./Projects.scss";

const Projects = () => {
  useEffect(() => {
    mixitup(document.querySelector(".project-wrapper"));
  }, []);

  const handleActive = (e) => {
    document.querySelectorAll(".project--list").forEach((e) => {
      e.classList.remove("active");
    });
    e.target.classList.add("active");
  };

  return (
    <motion.div
      className="projects"
      initial="out"
      animate="in"
      exit="out"
      variants={pageAnimation}
      transition={pageTransition}
    >
      <h2 className="text-center">
        <span>{projectsData.mainData.title}</span>
        {projectsData.mainData.title2}
      </h2>
      <p className="text-center">{projectsData.mainData.mainPara}</p>

      <ul className="projects--nav--list list-unstyled text-center">
        {projectsData.navigationList.map((listItem, index) => (
          <li
            key={listItem.id}
            className={`project--list ${index === 0 ? "active" : ""} ${
              listItem.classes
            }`}
            data-filter={listItem.dataFilter}
            onClick={handleActive}
          >
            {listItem.title}
          </li>
        ))}
      </ul>
      <div className="project-wrapper">
        <div className="row">
          {projectsData.projects.map((item) => (
            <Project
              key={item.id}
              id={item.id}
              classes={item.classes}
              img={item.img}
              alt={item.alt}
              section={item.section}
              title={item.heading}
              desc={item.description}
              link={item.href}
            />
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default Projects;
