import React from "react";

import "./Input.scss";

export const Input = ({ type, name, onClick, onChange, onFocus, onBlur }) => {
  return (
    <input
      name={name}
      className="form-control"
      onClick={onClick}
      type={type}
      onChange={onChange}
      onFocus={onFocus}
      onBlur={onBlur}
    />
  );
};
