import React from "react";

import "./TextArea.scss";

export const TextArea = ({
  type,
  name,
  onClick,
  onChange,
  onFocus,
  onBlur
}) => {
  return (
    <textarea
      name={name}
      className="form-control"
      cols="30"
      rows="7"
      onClick={onClick}
      type={type}
      onChange={onChange}
      onFocus={onFocus}
      onBlur={onBlur}
    />
  );
};
