import React from "react";
import { Tilt } from "../../../Tilt/Tilt";

const AboutList = ({
  id,
  listTitle,
  listTitleIcon,
  list,
  subTitleOne,
  subTitleOneIcon,
  subTitleTwo,
  subTitleTwoIcon,
  listTwo
}) => {
  return (
    <Tilt
      className="about-me personal"
      options={{ speed: 0, max: 1, reverse: true }}
    >
      <h3 className="personal-head">
        {listTitleIcon}
        {listTitle}
      </h3>
      <h4>
        {subTitleOneIcon}
        {subTitleOne}
      </h4>
      <ul>{list}</ul>
      <h4>
        {subTitleTwoIcon}
        {subTitleTwo}
      </h4>
      <ul>{listTwo}</ul>
    </Tilt>
  );
};

export default AboutList;
