import React from "react";
import navbarData from "./NavbarData";
import { Link } from "react-router-dom";

import "./Navbar.scss";

const Navbar = () => {
  const handleActive = (event) => {
    document.querySelectorAll(".link--item").forEach((e) => {
      e.classList.remove("active");
    });
    event.target.classList.add("active");
  };

  const removeActiveBrand = () => {
    document.querySelectorAll(".link--item").forEach((e) => {
      e.classList.remove("active");
    });
  };

  return (
    <header>
      <Link to="/" className="brand" onClick={removeActiveBrand}>
        <span className="colored">DEV</span>AH
      </Link>
      <div className="nav--links">
        {navbarData.map((elements, index) => (
          <Link
            key={index}
            className={elements.className}
            onClick={handleActive}
            to={elements.path}
          >
            {elements.name}
          </Link>
        ))}
      </div>
    </header>
  );
};

export default Navbar;
