import React from "react";
import contactData from "./ContactData";
import Form from "./ContactForm/Form";
import ContactInfo from "./ContacttInfo/ContactInfo";
import SocialIcons from "./SocialIcons/SocialIcons";
import { motion } from "framer-motion";
import { pageAnimation, pageTransition } from "../../PageTransition/index";

import "./Contact.scss";

const Contact = () => {
  return (
    <motion.div
      className="contact"
      initial="out"
      animate="in"
      exit="out"
      variants={pageAnimation}
      transition={pageTransition}
    >
      <h2 className="text-center">
        <span>{contactData.mainData.title}</span>{" "}
        {contactData.mainData.titleSpan}
      </h2>
      <p className="text-center">{contactData.mainData.paragraph}</p>
      <div className="form-section">
        {/* Form */}
        <Form formData={contactData.contactForm} />
        {/* Contact Addresses Component */}
        <h3 className="text-center">Contact Info</h3>
        <div className="row con-info">
          {contactData.contactInfo.map((item) => (
            <ContactInfo
              key={item.id}
              icon={item.icon}
              address={item.adress}
              className={item.className}
            />
          ))}

          {/* Social Media Icons Component */}
          <div className="col-md-12">
            <div className="social-icons text-center">
              {contactData.socialIcons.map((item) => (
                <SocialIcons
                  key={item.id}
                  path={item.path}
                  classs={item.class}
                  icon={item.iconName}
                  iconClass={item.iconClass}
                />
              ))}
            </div>
          </div>
          {/* End Social Media Icons Component */}
        </div>
      </div>
    </motion.div>
  );
};

export default Contact;
