import React from "react";
import { Link } from "react-router-dom";

import resumeImg from "../../../../assets/resume.png";
import { Button } from "../../../Button/Button";
import { Tilt } from "../../../Tilt/Tilt";

import { IoIosSend } from "react-icons/io";
import { HiDocumentDownload } from "react-icons/hi";
import { IconContext } from "react-icons";

const Bio = ({
  profile,
  resume,
  heading,
  headingSpan,
  paragraphOne,
  paragraphTwo,
  hireBtn,
  resumeBtn,
}) => {
  const addActive = () => {
    document.querySelector(".contact--link").classList.add("active");
  };
  return (
    <IconContext.Provider
      value={{ color: "var(--main-color)", className: "global-class-name" }}
    >
      {/* Profile Image */}
      <Tilt className="avatar" options={{ scale: 1.04, speed: 5000, max: 30 }}>
        <img className="img-fluid" src={profile} alt="profile" />
      </Tilt>

      {/* Bio Text */}
      <div className="text-center">
        <h1 className="text-center">
          {heading}
          <span className="colored">{headingSpan}</span>
        </h1>
        <p className="text-center">
          {paragraphOne} &amp; <br /> {[paragraphTwo]}
        </p>
      </div>

      {/* Buttons */}
      <div className="bio--links text-center">
        <Link to="/contact" onClick={addActive}>
          <Button to="/contact">
            <IoIosSend className="link--icon hire-icon" />
            {hireBtn}
          </Button>
        </Link>
        <a href={resumeImg} target="_blank">
          <Button buttonStyle="btn--outline">
            <HiDocumentDownload className="link--icon resume-icon" />
            {resumeBtn}
          </Button>
        </a>
      </div>
    </IconContext.Provider>
  );
};

export default Bio;
