import React from "react";
import { Button } from "../../../Button/Button";
import { Tilt } from "../../../Tilt/Tilt";

const Project = ({ id, classes, img, alt, section, title, desc, link }) => {
  return (
    <div className={`col-xl-4 col-md-6 mix project ${classes}`}>
      <Tilt className="project" options={{ speed: 5, max: 5, reverse: true }}>
        <img className="img-fluid" src={img} alt={alt} />
        <div className="info">
          <span className="section">{section}</span>
          <h3 className="title">{title}</h3>
          <p className="desc lead">{desc}</p>
          <a href={link} target="_blank">
            <Button>VISIT PORJECT</Button>
          </a>
        </div>
      </Tilt>
    </div>
  );
};

export default Project;
