import React, { useEffect, useState } from "react";
import { Input } from "../../../Input/Input";
import { TextArea } from "../../../TextArea/TextArea";

import { Button } from "../../../Button/Button";
import { FaPaperPlane } from "react-icons/fa";

const Form = ({ formData }) => {
  const check = (e) => {
    if (e.target.value !== "") {
      e.target.parentElement.classList.add("label-data");
    }
  };

  return (
    <form method="POST" action="https://formspree.io/a.grinta@hotmail.com">
      <h3 className="text-center">Send Message</h3>
      <div className="row">
        <div className="col-md-6">
          {formData.input.map((item, index) => (
            <div className="form-group" key={item.id}>
              <Input type={item.type} name={item.name} onBlur={check} />
              <label>
                {item.icon}
                {item.labelFor}
              </label>
            </div>
          ))}
        </div>

        <div className="col-md-6">
          <div className="form-group">
            <TextArea onBlur={check} />
            <label>
              {formData.textarea.icon}
              {formData.textarea.labelFor}
            </label>
          </div>
        </div>
        <div className="col-12 form-group hidden-sm hidden-xs">
          <Button
            buttonSize="btn--large"
            type="submit"
            className="btn btn-lg btn-block hvr-ripple-out"
          >
            Send
            <FaPaperPlane className="icon" />
          </Button>
        </div>
      </div>
    </form>
  );
};

export default Form;
