import React from "react";
import aboutData from "./AboutData";
import AboutList from "./AboutList/AboutList";
import { motion } from "framer-motion";
import { pageAnimation, pageTransition } from "../../PageTransition/index";

import "./About.scss";

const About = () => {
  return (
    <motion.div
      className="about"
      initial="out"
      animate="in"
      exit="out"
      variants={pageAnimation}
      transition={pageTransition}
    >
      <h2 className="text-center">
        <span>{aboutData.mainAboutData.title}</span>{" "}
        {aboutData.mainAboutData.titleTwo}
      </h2>
      <p className="text-center">{aboutData.mainAboutData.mainPara}</p>
      <div className="row">
        {aboutData.aboutListsData.map((item, index) => (
          <AboutList
            key={index}
            id={item.id}
            listTitle={item.listTitle}
            listTitleIcon={item.headingIcon}
            list={item.menuList.map((itemList) => (
              <li>{itemList}</li>
            ))}
            subTitleOne={item.subTitleOne}
            subTitleOneIcon={item.subTitleOneIcon}
            subTitleTwo={item.subTitleTwo}
            subTitleTwoIcon={item.subTitleTwoIcon}
            listTwo={item.secondList.map((itemList) => (
              <li>{itemList}</li>
            ))}
          />
        ))}
      </div>
    </motion.div>
  );
};

export default About;
